import React from 'react';
import Containter from 'reactstrap';
import Img from "gatsby-image";

export default class Header extends React.Component {
  render() {
    return (
      <header className="d-flex">
        <Img
          alt="Logo Heliski Albania"              
          sizes={this.props.headerImage.sizes}
          className=""
          style={{
            position: "absolute",
            left: 0,
            top: 0,
            width: "100%",
            height: "100%",
            zIndex: -2
          }}
        />
        
        <div className="container-fluid text-center my-auto">
          <h1 className="mb-1">Heliski Albania</h1>
          <h3 className="mb-5">
            <em>Ride first decents in unexplored Albanian alps</em>
          </h3>
          <a className="btn btn-primary btn-xl js-scroll-trigger m-2" href="#expiriance">Find out more</a>
          <a className="btn btn-xl btn-outline-primary m-2" href="/booking.html">See the packages</a>
        </div>
        <div className="overlay"></div>  
         
      </header>
    );
  }
}





