import React from 'react';
import Containter from 'reactstrap';
import Img from "gatsby-image";

export default class SectionPackages extends React.Component {
  render() {
    return (
      <section className="d-flex packages">
        <Img
          alt="Logo Heliski Albania"              
          sizes={this.props.packagesImage.sizes}
          className=""
          style={{
            position: "absolute",
            left: 0,
            top: 0,
            width: "100%",
            height: "100%",
            zindex: -1,
            objectposition: "center right"
          }}
        />
        <div className="container my-auto">
          <div className="mx-auto text-center mt-5">
            <h1 className="">Packages</h1>
            
          </div>
          <div className="card-deck mb-3 text-center">
            <div className="card mb-4 box-shadow">
              <div className="card-header card-header-primary">
                <h4 className="my-0 font-weight-normal">One week</h4>
              </div>
              <div className="card-body">
                <ul className="list-unstyled mt-3 mb-4">
                  <li><i className="far fa-user-circle fa-2x m-1"></i><i className="far fa-user-circle fa-2x m-1"></i><i className="far fa-user-circle fa-2x m-1"></i><i className="far fa-user-circle fa-2x m-1"></i><i className="fas fa-user-circle fa-2x m-1"></i><br/><strong>small groups</strong> of 4 skiers and 1 guide</li>
                  <li><strong>4</strong><br/>hobs <strong>hours</strong> of flying time</li>
                  <li><strong>6</strong><br/> days of skiing</li>
                  <li><strong>7</strong><br/> nights with full board accommodation</li>
                </ul>
              </div>
            </div>
            <div className="card mb-4 box-shadow">
              <div className="card-header card-header-primary">
                <h4 className="my-0 font-weight-normal">Private</h4>
              </div>
              <div className="card-body">
                <ul className="list-unstyled mt-3 mb-4">
                  <li><strong>1500</strong><br/>whole operation and 1500 square km of mountains for yourself</li>
                  <li><strong>12</strong><br/>max people</li>
                  <li>from 8 to 12 (hobs) hours of flying time</li>
                  <li>7 nights with full board accommodation 4 da gang</li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}





